body {
  margin: 0;
  padding: 0;
  font-family: Acme, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#96CCFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    margin:0 !important;
}

.bg-light-red{
    background-color: #122f2c;
    color:#fff;
}
.btn:hover{
    background-color: #0c1b17;
}

.inputBG{
    border-bottom: 1px solid #0c1b17;
    color:black !important;
}

.inputbg{
    border: 1px solid #0c1b17;
}

.fa{
    cursor: pointer;
    padding:2px;
}
.btn:focus, .inp:focus{
    outline: none !important;
}

.inp::-webkit-input-placeholder{
    color:rgb(26, 21, 21);
}

.inp::-ms-input-placeholder{
    color:rgb(26, 21, 21);
}

.inp::placeholder{
    color:rgb(26, 21, 21);
}

.delete {
    float: right;
    padding: 3px 5px;
    -webkit-transform:translateY(-3px);
            transform:translateY(-3px);
    background-color: rgb(117, 38, 18);
    color: #fff;
    border-radius: 2px;
    text-align: center;
}

.delete:hover{
    background-color: rgb(102, 11, 11)
}
.b--gray:hover {
    border-left: 4px solid #122f2c !important;
}

